import { partNumber, htmlLeftRightBlank } from "../constant/regex"
import { htmlEntityMap } from "../constant/map"

/**
 * 
 */
export function splitString(str: string, stepCharCount: number): string[] {
    if (!str || stepCharCount <= 0) {
        return []
    }
    const strLen = str.length
    if (strLen <= stepCharCount) {
        return [str]
    }
    let result: string[] = []
    let startIndex = 0
    while (startIndex < strLen) {
        result.push(str.substr(startIndex, stepCharCount))
        startIndex += stepCharCount
    }
    return result
}

/**
 * 
 * @param str 
 * @param len 
 * @param ellipsisChars 
 */
export function ellipsis(str: string, len: number, ellipsisChars = "..."): string {
    if (!str || len <= 0) {
        return ""
    }
    if (str.length <= len) {
        return str
    }
    return str.substring(0, len) + ellipsisChars
}

/**
 * "abc123.01cde"->123.01
 */
export function getNumber(str: string): number | null {
    if (!str) {
        return null
    }
    const mt = str.match(partNumber)
    if (!mt || !mt.length) {
        return null
    }
    return parseFloat(mt[0])
}

/**
 * 
 * @param  str 
 * @returns  
 */
export function trim(str: string) {
    if (!str) {
        return ""
    }
    return str.replace(/^\s+|\s+$/g, "")
}

/**
 * 
 * @param  str 
 * @returns  
 */
export function lTrim(str: string) {
    if (!str) {
        return ""
    }
    return str.replace(/^\s+/, "")
}

/**
 * 
 * @param  str 
 * @returns  
 */
export function rTrim(str: string) {
    if (!str) {
        return ""
    }
    return str.replace(/\s+$/, "")
}

/**
 * 
 * @param str 
 * @param strToRemove 
 * @param isIgnoreCase 
 * @returns 
 */
export function lTrimString(str: string, strToRemove: string, isIgnoreCase: boolean = false) {
    if (!str) {
        return ""
    }
    if (!strToRemove) {
        return str
    }
    return str.replace(new RegExp(`^(${strToRemove})*`, isIgnoreCase ? "gi" : "g"), "")
}

/**
 * 
 * @param str 
 * @param strToRemove 
 * @param isIgnoreCase 
 * @returns 
 */
export function rTrimString(str: string, strToRemove: string, isIgnoreCase: boolean = false) {
    if (!str) {
        return ""
    }
    if (!strToRemove) {
        return str
    }
    return str.replace(new RegExp(`(${strToRemove})*$`, isIgnoreCase ? "gi" : "g"), "")
}

/**
 * 
 * @param str 
 * @param strToRemove 
 * @param isIgnoreCase 
 * @returns 
 */
export function trimString(str: string, strToRemove: string, isIgnoreCase: boolean = false) {
    if (!str) {
        return ""
    }
    if (!strToRemove) {
        return str
    }
    return str.replace(new RegExp(`(^(${strToRemove})*)|((${strToRemove})*$)`, isIgnoreCase ? "gi" : "g"), "")
}

/**
 * html
 * @param str 
 * @returns 
 */
export function trimHTML(str: string) {
    if (!str) {
        return ""
    }
    while (htmlLeftRightBlank.test(str)) {
        str = str.replace(htmlLeftRightBlank, "")
    }
    return str
}

/**
 * sourceStrstr
 * @param  sourceStr 
 * @param  str 
 * @param  isIgnoreCase (false)
 * @returns  
 */
export function contains(sourceStr: string, str: string, isIgnoreCase: boolean = false) {
    if (!sourceStr) {
        return false
    }
    if (str === "") {
        return true
    }
    if (!str) {
        return false
    }
    if (isIgnoreCase) {
        sourceStr = sourceStr.toUpperCase()
        str = str.toUpperCase()
    }
    return sourceStr.includes(str)
}

/**
* html
* @param  html html
* @returns  
*/
export function escapeHtml(html: string) {
    if (!html) {
        return ""
    }
    return String(html).replace(/[&<>"'\/]/g, (s) => {
        return htmlEntityMap[s]
    })
}

/**
 * @param   str 
 * @param   count 
 * @returns  
 */
export function repeat(str: string, count: number) {
    if (!str || count <= 0) {
        return ""
    }
    let s = []
    while (count--) {
        s.push(str)
    }
    return s.join('')
}

/**
 * "+"appendtoString
 */
export class builder<T> {
    private _arr: T[] = []
    /**
     * 
     * @param items 
     * @returns this 
     */
    append(...items: T[]) {
        this._arr.push(...items)
        return this
    }
    /**
     * 
     */
    toString() {
        return this._arr.join("")
    }
    /**
     * 
     * @returns this
     */
    clear() {
        this._arr = []
        return this
    }
    /**
     * 
     */
    length() {
        return this.toString().length
    }
}