import { AnyKeyValueType } from "../declaration/common"
import {isArray} from "./data"

/**
   * key
   * @param json json
   * @param keyName key
   * @returns 
   */
export function hasKey(json: AnyKeyValueType, keyName: string) {
    if (!json) {
        return false
    }
    return Object.keys(json).includes(keyName)
}

/**
 * value
 * @param  json json
 * @param keyValue value
 * @returns 
 */
export function hasValue(json: AnyKeyValueType, keyValue: any) {
    if (!json) {
        return false
    }
    let r = false
    for (var k in json) {
        if (json[k] === keyValue) {
            r = true
            break
        }
    }
    return r
}

/**
 * jsonparam{a:1,b:2,c:[3,4,5]}=>"a=1&b=2&c=3&c=4&c=5"
 * @param  json json
 * @returns 
 */
export function toParams(json: AnyKeyValueType) {
    if (!json) return "";
    let arr = [], temp = "";
    for (let m in json) {
        if (isArray(json[m])) {
            temp = json[m].join("&" + m + "=");
        } else {
            temp = json[m];
        }
        arr.push(m + "=" + temp);
    }
    return arr.join("&");
}