/**
 * 
 */
export class KeyValue {
    constructor(key: string, value: any, extend?: any) {
        this.key = key
        this.value = value
        this.extend = extend
    }
    /**
     * 
     */
    key: string = ""
    /**
     * value
     */
    value: any
    /**
     * 
     */
    extend: any
}

/**
 * 
 */
export class KeyNameValue {
    constructor(key: string, name: string, value: any, extend?: any) {
        this.key = key
        this.name = name
        this.value = value
        this.extend = extend
    }
    /**
     * 
     */
    key: string = ""
    /**
     * name
     */
    name: string = ""
    /**
     * value
     */
    value: any
    /**
     * 
     */
    extend: any
}