import { document } from "./lib"

//referencehttps://developer.mozilla.org/zh-CN/docs/Web/API/Document/cookie

/**
 * cookie
 * @param sKey cookie
 * @returns 
 */
export function getItem(sKey: string): string | null {
    if (!document) {
        return null
    }
    return decodeURIComponent(document.cookie.replace(new RegExp("(?:(?:^|.*;)\\s*" + encodeURIComponent(sKey).replace(/[-.+*]/g, "\\$&") + "\\s*\\=\\s*([^;]*).*$)|^.*$"), "$1")) || null;
}

/**
 * cookie
 * @param sKey 
 * @param sValue 
 * @param vEnd Number/String/DateNumbermax-ageStringDateexpires
 * @param sPath /
 * @param sDomain ""
 * @param bSecure httpsfalse
 * @returns 
 */
export function setItem(sKey: string, sValue: string, vEnd: number | string | Date, sPath: string = "/", sDomain: string = "", bSecure: boolean = false): boolean {
    if (!document) {
        return false
    }
    if (!sKey || /^(?:expires|max\-age|path|domain|secure)$/i.test(sKey)) {
        return false
    }
    let sExpires = ""
    if (vEnd) {
        switch (vEnd.constructor) {
            case Number:
                sExpires = vEnd === Infinity ? "; expires=Fri, 31 Dec 9999 23:59:59 GMT" : "; max-age=" + vEnd
                break
            case String:
                sExpires = "; expires=" + vEnd
                break
            case Date:
                sExpires = "; expires=" + (vEnd as Date).toUTCString()
                break
        }
    }
    document.cookie = encodeURIComponent(sKey) + "=" + encodeURIComponent(sValue) + sExpires + (sDomain ? "; domain=" + sDomain : "") + (sPath ? "; path=" + sPath : "") + (bSecure ? "; secure" : "")
    return true
}

/**
 * cookie
 * @param sKey 
 * @param sPath /
 * @param sDomain ""
 * @returns 
 */
export function removeItem(sKey: string, sPath: string = "/", sDomain: string = ""): boolean {
    if (!document) {
        return false
    }
    if (!sKey || !hasItem(sKey)) {
        return false
    }
    document.cookie = encodeURIComponent(sKey) + "=; expires=Thu, 01 Jan 1970 00:00:00 GMT" + (sDomain ? "; domain=" + sDomain : "") + (sPath ? "; path=" + sPath : "")
    return true
}

/**
 * cookie
 * @param sKey 
 * @returns 
 */
export function hasItem(sKey: string): boolean {
    if (!document) {
        return false
    }
    return (new RegExp("(?:^|;\\s*)" + encodeURIComponent(sKey).replace(/[-.+*]/g, "\\$&") + "\\s*\\=")).test(document.cookie)
}

/**
 * cookie
 * @returns 
 */
export function keys(): string[] {
    if (!document) {
        return []
    }
    let aKeys = document.cookie.replace(/((?:^|\s*;)[^\=]+)(?=;|$)|^\s*|\s*(?:\=[^;]*)?(?:\1|$)/g, "").split(/\s*(?:\=[^;]*)?;\s*/)
    for (let nIdx = 0; nIdx < aKeys.length; nIdx++) {
        aKeys[nIdx] = decodeURIComponent(aKeys[nIdx])
    }
    return aKeys
}