/**
 * 
 */
export class SelectItem {
    constructor(text: string, value: any, isSelected: boolean = false, extend?: any) {
        this.text = text
        this.value = value
        this.isSelected = isSelected
        this.extend = extend
    }
    /**
     * 
     */
    text: string = ""
    /**
     * 
     */
    isSelected = false
    /**
     * value
     */
    value: any
    /**
     * 
     */
    extend: any
}